<template lang="pug">
  
  .payment-buisness
    PaymentHeader
      template
        h1.payment-header__title Перевод на счет

    PaymentOrder(:amount='parseFloat(order.amount)' @submit='submit')
      template
        .bank-payment__item
          p.bank-payment__order-name Номер счета:
          input(type='number' v-model.trim='order.id')
        .bank-payment__item
          p.bank-payment__order-name Сумма платежа:
          input(type='number' v-model.trim='order.amount' @input='paymentData')

</template>

<script>

  
  import PaymentHeader from './PaymentHeader'
  import PaymentOrder from './PaymentOrder'

  import {required, between} from 'vuelidate/lib/validators'

  export default {
    components: { PaymentHeader, PaymentOrder },
    data() {
      return {
        order: {
          id: '',
          amount: ''
        }
      }
    },
    methods: {
      paymentData() {
        if (this.order.amount.length >= 9) 
          return this.order.amount = this.order.amount.slice(0,9)
      },
      submit() {
        this.$v.$touch()

        if (!this.$v.order.id.required || !this.$v.order.amount.required)
          return this.$alert.show('Заполните все поля')
        
        else if (!this.$v.order.amount.between)
          return this.$alert.show('Введите значение от 1 до 100 000')

        this.$router.go(-1)
        this.$alert.show(`Платеж на сумму $${this.order.amount} осуществлен`)
        this.$store.commit('MAKE_TRANSACTION', {
          title: 'Перевод на счет', type: 'withdraw', money: this.order.amount
        })
      }
    },
    validations: {
      order: {
        id: {required},
        amount: {required, between: between(1,100000)}
      }
    }
  }

</script>

