<template lang="pug">
  
  .bank-payment__order
    h1.bank-payment__title Детали платежа:
    .bank-payment__inputs
      slot

      button(@click='submit').bank-payment__confirm.
        Оплатить #[b ${{isNaN(getAmountComission) ? 0 : getAmountComission}}]
      
      p.bank-payment__comission Коммиссия: {{comission}}%

</template>

<script>
  import {mapState} from 'vuex'

  export default {
    props: { amount: Number },
    methods: {
      submit() {
        if (this.balance < this.getAmountComission)
          return this.$alert.show('Недостаточно средств')

        this.$emit('submit')
      }
    },
    computed: {
      ...mapState({ 
        comission: state => state.bank.commissionPercent,
        balance: state => state.bank.balance
      }),
      getAmountComission() {
        return this.amount + this.comission * this.amount / 100
      }
    }
  }

</script>
